.Ican {
    background-color: aqua;
    width: 100%;
    height: 900px;
    padding: 50px 0;
    display: grid; /* Добавляем grid */
    grid-template-columns: repeat(4, 1fr);
    gap: 50px;
    align-items: center;
}

.Ican{
   
        background-image: url('../images/p2.png');
        background-size: cover;
        background-position: center;
        background-attachment: fixed;
        height: 100vh;
        width: 100%;
    
}
.I_can {
    font-size: 8rem;
    position: absolute;
    top: 1650px;
    left: 610px;
    font-weight: bold;
    color: transparent;
    background-image: url('https://avatars.mds.yandex.net/i?id=0680eb8c1f672602ac896e330f5842f2fa0a5354-4406655-images-thumbs&n=13'); 
    background-size: 200%; /* Enlarged for smooth animation */
    background-position: 0 50%;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: animate-background 5s infinite alternate linear;
}

@keyframes animate-background {
    0% {
        background-position: 0 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}







.skill_div{
    width:330px;
    height: 500px;
    background-color: aqua;
    transition:.2s all; 
   border-radius:10px ;
   border: 4px solid black;
   align-items: center;
   text-align: center;
}


.Ican:hover .skill_div{
    filter:blur(3px);
    opacity:.5;
    transform: scale(.98);
    box-shadow:none;
  }
  .Ican:hover .skill_div:hover{
    transform:scale(1);
    filter:blur(0px);
    opacity:1;
    box-shadow:0 8px 20px 0px rgba(0,0,0,0.125);
  }

  .skill_d_h2{
    color:darkgreen;
    font-family: "Montserrat", serif;
    font-optical-sizing: auto;
   
    font-style: normal;
  }
  .skill_d_p{
    font-size: 20px;
    font-family: "Montserrat", serif;
    font-optical-sizing: auto;
    color:darkblue;
    font-style: normal;
    width: 250px;
    padding-left: 40px;
  }